.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.byline,
.updated:not(.published){
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}
section.hero {
	width: 100%;
	float: left;
	.hero-text {
		color: #fff;
		width: 90%;
		margin: 10% auto 30% auto;
		text-align: center;
		line-height: 1.2;
		background-color: rgba(0,0,0,.5);
		padding: 10px;
		font: {
			size: 22px;
		}
		@media screen and (min-width: $tabwidth) {
			width: 80%;
			background-color: rgba(0,0,0,.0);
			padding: 0;
			font-size: 28px;
		}
		@media screen and (min-width: $deskwidth) {
			width: 50%;
			font-size: 37px;
		}
	}
}
.smenu-item {
	width: 90%;
	display: inline-block;
	margin: 20px auto;
	position: relative;

	@media screen and (min-width: $tabwidth) {
		width: 40%;
		margin: 2%;
	}
	@media screen and (min-width: $deskwidth) {
		width: 20%;
	}

	.menu-image {
		width: 100%;
		// border-left: 1px solid #eee;
		// border-bottom: 1px solid #eee;
		// border-right: 1px solid #eee;
		float: left;
		line-height: 0;
		img {
			width: 100%;
		}
	}
}
.menu-top-button {
	width: 100%;
	float: left;
	padding: 20px 0;
}
.menu-top-button-page {
	width: auto;
	padding: 20px 0;
}
.gal {
	width: 100%;
	float: left;
	line-height: 0;
	
	position: relative;
	@media screen and (min-width: $tabwidth) {
		width: 45%;
	}

	img {
		width: 100%;
	}
	&.first {
		margin: 0 0 10px 0;
		@media screen and (min-width: $tabwidth) {
			margin: 0 9% 20px 0;
		}
	}
	&.last {
		margin: 0 0 10px 0;
		@media screen and (min-width: $tabwidth) {
			margin: 0 0% 20px 0;
		}
	}
	.caption {
		width: 100%;
		padding: 10px 0 15px 0; 
		background-color: rgba(0,0,0,.7);
		color: #fff;
		position: absolute;
		bottom: 0;
		left: 0;
		margin: 0 !important;
		text-align: center;
		line-height: 1;
	}
}